<template>
  <div>
    <TopNav mdl="coupon" select="1"></TopNav>
    <div class="wyu-main" v-loading="loading">
      <div class="wyu-block">
        <div class="wyu-tips wyu-tips-info" style="margin-bottom:10px;">
          <p class="tit"> 1）这里设置优惠券的使用说明、弹出间隔等</p>
        </div>
        <div class="wyu-title">
          <h2>优惠券设置</h2>
        </div>
        <el-form ref="form" :model="form" label-width="120px" size="mini">
          <el-form-item label="是否弹出" >
            <el-switch :v-model="form.coupon_popup" :value="form.coupon_popup"  :active-value="1" :inactive-value="0"></el-switch>
          </el-form-item>

          <el-form-item label="弹出间隔" >
            <el-input v-model="form.coupon_interval" style="width: 300px" placeholder="请输入弹出间隔"></el-input>
            <p>这里的产出间隔单位是小时</p>
          </el-form-item>

          <el-form-item label="使用说明" >
            <html-editor title="使用说明" :html="form.coupon_direction"  v-model="form.coupon_direction"></html-editor>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <div class="wyu-btn-div">
      <el-button size="small" type="primary"  @click="setCouponSetting()">立即保存</el-button>
      <el-button size="small">取消</el-button>
    </div>
  </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import HtmlEditor from "@/components/HtmlEditor";
    import WyuUpload from "@/components/Photos";

    import * as api from "@/api";
    import AmapDr from "../../../components/AmapDr";
    export default {
        name: 'adminRole',
        components: {AmapDr, TopNav,HtmlEditor,WyuUpload},
        data() {
            return {
                loading: false,

                form: {
                    coupon_popup: 0,
                    coupon_interval: '',
                    coupon_direction: [],

                }
            }
        },

        created() {
            this.getCouponSetting();
        },

        methods: {
            getCouponSetting(){
                this.loading = true;
                api.post("/shop/admin.setting/getCoupon", {
                    appToken: this.$store.getters.appToken,
                }).then(res => {
                    this.form = res;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },

            setCouponSetting() {
                this.loading = true;
                api.post("/shop/admin.setting/setCoupon", {
                    appToken: this.$store.getters.appToken,
                    coupon_popup: this.form.coupon_popup,
                    coupon_interval: this.form.coupon_interval,
                    coupon_direction: JSON.stringify(this.form.coupon_direction),
                }).then(res => {
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success',
                    });
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            }
        }
    }
</script>
